import * as React from 'react';
import { PageProps } from 'gatsby';
import defaultData from './defaultData';

interface ContactProps extends React.HTMLProps<HTMLDivElement> {}

interface ContactState {
  iframeWidth: number;
  iframeHeight: number;
  isDisplayForm: boolean;
}

export default class extends React.Component<ContactProps, ContactState> {
  constructor(props) {
    super(props);

    //  state 
    this.state = {
      iframeWidth: 640,
      iframeHeight: 1152,
      isDisplayForm: false
    };
  }

  componentDidMount() {
    this.setState({
      isDisplayForm: /displayForm/.test(window.location.search)
    });
  }

  public render() {
    return (
      <section id='contact'>
        <div className='container'>
          <h1 className={this.state.isDisplayForm ? 'd-none' : ''}>
            {defaultData.contact.title}
          </h1>
          <div className='contact-background'>
            <div className='row'>
              <div className='col-10 offset-1 col-lg-8 offset-lg-2'>
                <p className={this.state.isDisplayForm ? 'd-none' : ''}>
                  
                </p>
                <div className='col-12 col-xl-4 offset-xl-4'>
                  <a href='https://forms.gle/hh4mNqLmxFar7krb6' target='_blank'>
                    <button
                      type='button'
                      className={`btn btn-lg btn-secondary${
                        this.state.isDisplayForm ? ' d-none' : ''
                      }`}
                    >
                      
                    </button>
                  </a>
                </div>
                <iframe
                  className={`inquiry-form${
                    this.state.isDisplayForm ? '' : ' d-none'
                  }`}
                  style={{ width: '100%' }}
                  src='https://docs.google.com/forms/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/viewform?embedded=true'
                  width={this.state.iframeWidth}
                  height={this.state.iframeHeight}
                  frameBorder={0}
                  marginWidth={0}
                  marginHeight={0}
                >
                  
                </iframe>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}
